.FAQCard {
  max-width: 800px;
  width: 100%;
  background-color: #153c65;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding-block: clamp(1.25rem, 1.042rem + 1.042vw, 1.875rem);
  padding-inline: clamp(1.875rem, 1.667rem + 1.042vw, 2.5rem)
    clamp(1.25rem, 1.042rem + 1.042vw, 1.875rem);
}

.FAQCard__questionWrapper {
  display: flex;
  gap: clamp(1.25rem, 0.833rem + 2.083vw, 2.5rem);
  justify-content: space-between;
  align-items: center;
}

.plusButton {
  /* align-self: flex-start; */
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.closeIcon {
  width: 40px;
  height: 40px;
  transition: transform 0.3s ease;
  transform: scale(1);
}

.plusButton--active {
  && .closeIcon {
    transform: scale(-1);
  }
}

.question {
  font-size: clamp(1.25rem, 1.125rem + 0.625vw, 1.625rem);
  font-weight: 600;
  line-height: 1.2;
  border-bottom: transparent 0px solid;
  transition: border-bottom 0.3s ease-in-out, padding-bottom 0.3s ease-in-out;
}

.question--open {
  border-bottom: #2a5a8c 2px solid;
  padding-bottom: 30px;
}

.answer {
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  line-height: 1.4;
  max-height: 0;
  overflow: hidden;
  padding-top: 0;
  transition: max-height 0.3s ease-in-out, padding-top 0.3s ease-in-out;
}

.answer--open {
  max-height: 200px;
  padding-top: clamp(1.25rem, 1.042rem + 1.042vw, 1.875rem);
}
