.ErrorPage {
  width: 100%;
  height: 50vw;
  background: linear-gradient(
      0deg,
      rgba(0, 31, 63, 1) 0%,
      rgba(0, 31, 63, 0) 100%
    ),
    url(../../img/pageBanner--eventheme.webp) no-repeat bottom center / cover;
}

.ErrorPage__wrap {
  margin: 180px auto 250px;
  max-width: 1280px;
}

.ErrorPage__info {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  text-wrap: balance;
}

.ErrorPage__h1 {
  font-size: clamp(3.438rem, 3.125rem + 1.563vw, 4.375rem);
  font-family: Arial Black, "Helvetica Neue", Helvetica, sans-serif;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.ErrorPage__p {
  font-size: clamp(1.125rem, 0.875rem + 1.25vw, 1.875rem);
  max-width: 1200px;
  line-height: 1.3;
}

@media screen and (max-width: 480px) {
  .ErrorPage {
    width: 100%;
    height: clamp(18.75rem, 6.25rem + 62.5vw, 25rem);
  }

  .ErrorPage__wrap {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}
