.link {
  max-width: 300px;
  width: 100%;
}

.ExtraServiceCard {
  max-width: 300px;
  width: 100%;
  display: grid;
  place-content: center;
  justify-items: center;
  padding: 30px 40px;
  border-radius: 10px;
  transform: scale(100%);
  transition: ease-in 0.15s transform;
  &&:hover {
    transform: scale(105%);
  }
}

.ExtraServiceCard--orange {
  transform: scale(110%);
  background-color: #ff851b;
  color: #001f3f;
  && .ExtraServiceCard__content h4 {
    font-size: 24px;
  }
}

.ExtraServiceCard--blue {
  background-color: #153c65;
  color: #ffffff;
}

.icon {
  width: 55px;
  margin-bottom: 30px;
}

.ExtraServiceCard__content {
  text-align: center;
  && h4 {
    font-size: clamp(1.125rem, 1.042rem + 0.417vw, 1.375rem);
    margin-bottom: 12px;
    text-transform: uppercase;
  }
  && p {
    margin-bottom: 22px;
    line-height: calc(clamp(1rem, 0.917rem + 0.417vw, 1.25rem) * 1.4);
    font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  }
}

@media screen and (max-width: 768px) {
  .ExtraServiceCard--orange {
    transform: scale(100%);}
}