.Header {
  padding: 40px 0px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1280px;
  position: relative;
}

.Header__logo {
  width: clamp(8.125rem, 6.518rem + 8.036vw, 13.75rem);
}

.Header__navbar {
  display: flex;
  align-items: center;
  gap: 40px;
  list-style: none;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: max-height 0.3s ease;
}

.link {
  position: relative;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
}

a {
  color: #fff;
  text-decoration: none;
}
a:hover,
.link:hover {
  color: #ff851b;
  text-decoration: underline;
}

.hamburgerMenu {
  display: none;
  cursor: pointer;
}

.hamburger {
  width: 40px;
  height: auto;
}

.dropdownMenu {
  display: none;
}

.dropdownLink {
  text-align: center;
  color: #ffffff;
  padding: 10px;
  display: block;
  text-decoration: none;
  font-size: 0.875rem;
}

.dropdownLink:hover {
  border-radius: 5px;
  background-color: #ff851b;
  color: #001f3f;
}

.link--services:hover {
  z-index: 1;
  text-decoration: none;
}

.link--services:hover::before{
  background-color: #153c65;
  content: "";
  position: absolute;
  width: 110px;
  height: 100%;
  top: -50%;
  left: -20%;
  background-color: #153c65;
  padding: 10px;
  border-radius: 10px 10px 0 0;
  z-index: -1;
}

.link--services:hover .dropdownMenu {
  display: block;
  width: 130px;
  position: absolute;
  background-color: #153c65;
  list-style: none;
  padding: 10px;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1;
  top: 100%;
  left: -20%;
}

@media screen and (max-width: 1280px) {
  .Header {
    margin-inline: clamp(1.25rem, 0.833rem + 2.083vw, 2.5rem);
  }
}

@media screen and (max-width: 768px) {
  .Header__navbar {
    flex-direction: column;
    position: absolute;
    top: 100%;
    right: 0;
    background-color: #153c65;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    gap: 0;
  }

  .Header__navbar.open {
    z-index: 2;
    max-height: 500px;
  }

  .hamburgerMenu {
    display: block;
  }

  .link:not(.link--services),
  .link--services {
    padding: 40px;
    text-align: center;
    border-bottom: rgba(255, 255, 255, 0.4) solid 1px;
  }

  .link--services:hover .dropdownMenu,
  .link--services:hover::before {
    display: none;
  }

  .Header__navbar > a:last-child > .link {
    border-bottom: none;
  }
}
