.AboutUsPage {
  width: 100%;
  height: 50vw;
  background: linear-gradient(
      0deg,
      rgba(0, 31, 63, 1) 0%,
      rgba(0, 31, 63, 0) 100%
    ),
    url(../../img/pageBanner--eventheme.webp) no-repeat bottom center / cover;
}

.AboutUsPage__section {
  margin: 180px auto;
  max-width: 1280px;
}

.AboutUsPage__banner {
  margin-top: 230px;
}

.section__h2 {
  font-size: clamp(1.625rem, 1.167rem + 2.292vw, 3rem);
  line-height: calc(clamp(1.625rem, 1.167rem + 2.292vw, 3rem) * 1.15);
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 70px;
  text-align: center;
}

.section__h2--faq {
  text-transform: none;
}

.banner__content {
  display: flex;
  justify-content: center;
  gap: 30px 60px;
  flex-wrap: wrap;
}

.AboutUsPage__infoBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.infoBlock__content {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  max-width: 1200px;
  gap: 40px;
}

.infoBlock__content--imgLeft {
  gap: 60px;
}

.infoBlock__text {
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.infoBlock__text {
  max-width: 580px;
}

.infoBlock__pWrap {
  gap: 20px;
  display: flex;
  flex-direction: column;
}

.infoBlock__h3 {
  font-size: clamp(1.375rem, 1.054rem + 1.607vw, 2.5rem);
  line-height: calc(clamp(1.375rem, 1.054rem + 1.607vw, 2.5rem) * 1.15);
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 30px;
}

.infoBlock__img {
  max-width: 560px;
  width: 100%;
}

.section__heading {
  text-align: center;
  margin-bottom: 86px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.extra__cards {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.section__h2 {
  margin-bottom: 56px;
}

.section__p,
.infoBlock__p {
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  line-height: calc(clamp(1rem, 0.917rem + 0.417vw, 1.25rem) * 1.4);
  max-width: 400px;
  text-wrap: balance;
}

.section__p--banner {
  max-width: 550px;
}

.infoBlock__p {
  max-width: none;
}

.FAQCardsWrapper,
.AboutUsPage__contactUs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.FAQCardsWrapper {
  gap: 40px;
}

.waves {
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  && svg {
    width: calc(100% + 1.3px);
    height: 90px;
  }
  && .shape-fill {
    fill: #153c65;
  }
}

@media screen and (max-width: 768px) {
  .AboutUsPage__faqs,
  .AboutUsPage__section {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}

@media screen and (max-width: 480px) {
  .AboutUsPage {
    width: 100%;
    height: clamp(18.75rem, 6.25rem + 62.5vw, 25rem);
  }

  .AboutUsPage__contactUs {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}
