.ServiceCard {
  max-width: 550px;
  width: 100%;
  background-color: #153c65;
}

.ServiceCard__img {
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  transition: 0.15s ease-in filter;
  height: 300px;

  &&:hover {
    filter: brightness(115%);
  }
}

.ServiceCard__content {
  padding: clamp(1.25rem, 1.042rem + 1.042vw, 1.875rem);
  && h3 {
    font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
    line-height: calc(clamp(1rem, 0.958rem + 0.208vw, 1.125rem) * 1.15);
    margin-bottom: 12px;
    text-transform: uppercase;
  }
  && p {
    font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
    margin-bottom: 22px;
    line-height: calc(clamp(1rem, 0.958rem + 0.208vw, 1.125rem) * 1.4);
  }
}

@media screen and (max-width: 1024px) {
  .ServiceCard {
    max-width: 400px;
  }
}