.ContactUsPage {
  width: 100%;
  height: 50vw;
  background: linear-gradient(
      0deg,
      rgba(0, 31, 63, 1) 0%,
      rgba(0, 31, 63, 0) 100%
    ),
    url(../../img/pageBanner--eventheme.webp) no-repeat bottom center / cover;
}

.ContactUsPage__section {
  margin: 180px auto;
  max-width: 1280px;
}

.ContactUsPage__contactUs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ContactUsPage__banner {
  margin-top: 230px;
}

.section__heading {
  text-align: center;
  margin-bottom: 86px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content__p,
.section__p {
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  line-height: calc(clamp(1rem, 0.917rem + 0.417vw, 1.25rem) * 1.4);
  text-wrap: balance;
}

.section__p {
  max-width: 500px;
}

.section__h2 {
  font-size: clamp(1.625rem, 1.167rem + 2.292vw, 3rem);
  line-height: calc(clamp(1.625rem, 1.167rem + 2.292vw, 3rem) * 1.15);
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 56px;
  text-align: center;
}

.contactUs__contentWrap,
.contactDetails__contentWrap {
  display: flex;
  flex-wrap: wrap;
  gap: clamp(3.125rem, 2.083rem + 5.208vw, 6.25rem);
  width: 100%;
  justify-content: center;
}

.contactFormWrap {
  max-width: 500px;
  width: 100%;
}

.content {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 80px;
}

.contentSection {
  display: flex;
  flex-direction: column;
}

.content__h3 {
  font-size: clamp(1.375rem, 1.196rem + 0.893vw, 2rem);
  line-height: calc(clamp(1.375rem, 1.196rem + 0.893vw, 2rem) * 1.15);
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 15px;
}

.content__p {
  margin-bottom: 40px;
}

.socialWrap {
  display: flex;
  gap: 10px;
}

.subscription__form,
.subscription__label {
  display: flex;
  flex-direction: column;
}

.subscription__form {
  gap: 30px;
}

.subscription__label {
  display: flex;
  flex-direction: column;
  font-size: clamp(1.125rem, 1.042rem + 0.417vw, 1.375rem);
  gap: 10px;
  text-transform: capitalize;
  width: 100%;
}

.subscription__input {
  border: 0;
  font-family: inherit;
  padding: 15px;
  border-radius: 5px;
  font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
}

.subscription__addText {
  font-size: clamp(0.875rem, 0.833rem + 0.208vw, 1rem);
  color: rgba(255, 255, 255, .7);
  line-height: calc(clamp(0.875rem, 0.833rem + 0.208vw, 1rem) * 1.4);
}

.contactDetails__map {
  max-width: 600px;
  width: 100%;
  height: 450px;
}

.contactDetails__wrap > .content__h3 {
  margin-bottom: 0;
}

.contactDetails__wrap {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.contactDetails__infoWrap {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.contactDetails__textWrap > .content__p {
  margin-bottom: 0;
}

.contactDetails__textWrap > .content__p:first-child {
  margin-bottom: 10px;
}

.contactDetails__textWrap > .content__p:nth-child(2n) {
  color: rgba(255, 255, 255, .7);
}

.waves {
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  && svg {
    width: calc(100% + 1.3px);
    height: 90px;
  }
  && .shape-fill {
    fill: #153c65;
  }
}

@media screen and (max-width: 480px) {
  .ContactUsPage {
    width: 100%;
    height: clamp(18.75rem, 6.25rem + 62.5vw, 25rem);
  }

  .ContactUsPage__section,
  .ContactUsPage__contactUs {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}