.ServicePage {
  width: 100%;
  height: 50vw;
}

.ServicePage__aboutService,
.ServicePage__portfolio,
.ServicePage__extra,
.ServicePage__faqs,
.ServicePage__contactUs {
  margin: 180px auto;
  max-width: 1280px;
}

.ServicePage__aboutService {
  margin-top: 230px;
}

.aboutService__h2,
.portfolio__h2,
.extra__h2,
.faqs__h2,
.contactUs__h2 {
  font-size: clamp(1.625rem, 1.167rem + 2.292vw, 3rem);
  line-height: calc(clamp(1.625rem, 1.167rem + 2.292vw, 3rem) * 1.15);
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 70px;
  text-align: center;
}

.aboutService__h2 {
  margin-bottom: 150px;
}

.aboutService__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 60px;
  max-width: 1200px;
}

.aboutService__text {
  max-width: 540px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.aboutService__p {
  text-wrap: balance;
  font-size: 20px;
  line-height: 1.4;
}

.aboutService__video {
  max-width: 560px;
  width: 100%;
}

.faqs__h2 {
  text-transform: none;
}

.portfolio__content {
  display: flex;
  justify-content: center;
  gap: 30px 80px;
  flex-wrap: wrap;
}

.portfolio__heading,
.extra__heading,
.faqs__heading,
.contactUs__heading {
  text-align: center;
  margin-bottom: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio__heading,
.extra__heading,
.faqs__heading,
.contactUs__heading {
  margin-bottom: 86px;
}

.extra__cards {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.portfolio__h2,
.extra__h2,
.faqs__h2,
.contactUs__h2 {
  margin-bottom: 56px;
}

.portfolio__p,
.extra__p,
.faqs__p,
.contactUs__p {
  font-size: 20px;
  line-height: 1.4;
  max-width: 400px;
  text-wrap: balance;
}

.FAQCardsWrapper,
.ServicePage__contactUs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.FAQCardsWrapper {
  gap: 40px;
}

.waves {
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  && svg {
    width: calc(100% + 1.3px);
    height: 90px;
  }
  && .shape-fill {
    fill: #153c65;
  }
}

@media screen and (max-width: 768px) {
  .ServicePage__aboutService,
  .ServicePage__portfolio,
  .ServicePage__extra,
  .ServicePage__faqs,
  .ServicePage__contactUs {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}

@media screen and (max-width: 480px) {
  .ServicePage {
    width: 100%;
    height: clamp(21.875rem, 15.625rem + 31.25vw, 25rem);
  }
}
