.MainServicesPage {
  width: 100%;
  height: 50vw;
  background: linear-gradient(
      0deg,
      rgba(0, 31, 63, 1) 0%,
      rgba(0, 31, 63, 0) 100%
    ),
    url(../../img/pageBanner--eventheme.webp) no-repeat bottom center / cover;
}

.MainServicesPage__services,
.MainServicesPage__extra,
.MainServicesPage__faqs,
.MainServicesPage__contactUs {
  margin: 180px auto;
  max-width: 1280px;
}

.MainServicesPage__services {
  margin-top: 230px;
}

.services__h2,
.extra__h2,
.faqs__h2,
.contactUs__h2 {
  font-size: clamp(1.625rem, 1.167rem + 2.292vw, 3rem);
  line-height: calc(clamp(1.625rem, 1.167rem + 2.292vw, 3rem) * 1.15);
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 70px;
  text-align: center;
}

.faqs__h2 {
  text-transform: none;
}

.services__content {
  display: flex;
  justify-content: center;
  gap: 30px 60px;
  flex-wrap: wrap;
}

.extra__heading,
.faqs__heading,
.contactUs__heading {
  text-align: center;
  margin-bottom: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.faqs__heading,
.contactUs__heading {
  margin-bottom: 86px;
}

.extra__cards {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.extra__h2,
.faqs__h2,
.contactUs__h2 {
  margin-bottom: 56px;
}

.extra__p,
.faqs__p,
.contactUs__p {
  font-size: 20px;
  line-height: 1.4;
  max-width: 400px;
}

.FAQCardsWrapper,
.MainServicesPage__contactUs {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.FAQCardsWrapper {
  gap: 40px;
}

.waves {
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  && svg {
    width: calc(100% + 1.3px);
    height: 90px;
  }
  && .shape-fill {
    fill: #153c65;
  }
}

@media screen and (max-width: 1024px) {
  .MainServicesPage__extra {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}

@media screen and (max-width: 768px) {
  .MainServicesPage__faqs {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}

@media screen and (max-width: 480px) {
  .MainServicesPage {
    width: 100%;
    height: clamp(18.75rem, 6.25rem + 62.5vw, 25rem);
  }

  .MainServicesPage__services,
  .MainServicesPage__contactUs {
    margin-inline: clamp(1.25rem, -0.75rem + 10vw, 1.875rem);
  }
}
