:root {
  --footer-col-max-width: 285px;
  --white-color: #fff;
  --accent-color: #ff851b;
}

.Footer {
  background-color: #153c65;
  padding: clamp(3.125rem, 1.875rem + 6.25vw, 6.875rem)
    clamp(1.25rem, 0.833rem + 2.083vw, 2.5rem)
    clamp(0.625rem, 0.208rem + 2.083vw, 1.875rem);
}

.Footer__colWrapper {
  max-width: 1280px;
  margin: 0 auto clamp(1.875rem, 1.083rem + 3.958vw, 4.25rem);
  display: flex;
  justify-content: space-between;
}

.info__logo {
  width: clamp(9.375rem, 7.625rem + 8.75vw, 14.625rem);
  margin-bottom: 16px;
}

.col {
  max-width: var(--footer-col-max-width);
}

.col--links,
.col--newsletter,
.col--contact {
  margin-top: 13px;
}

.col--links {
  max-width: 168px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 26px;
}

.links__item {
  list-style: none;
  font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
}

.links__link {
  color: var(--footer-link-color);
  text-decoration: none;
  &&:hover {
    text-decoration: underline;
  }
}

.subscription__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 20px;
}

.contact__h4,
.subscription__h4 {
  font-size: 1.25rem;
}

.subscription__input {
  padding: 0 13px 0 20px;
  border-radius: 20px;
  border: 0;
  font-family: inherit;
  height: 43px;
}

.newsletter__checkboxContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  && p {
    font-size: clamp(0.625rem, 0.542rem + 0.417vw, 0.875rem);
    line-height: calc(clamp(0.625rem, 0.542rem + 0.417vw, 0.875rem) * 1.3);
    text-align: left;
  }
}

.newsletter__checkboxContainer input:focus ~ .checkmark {
  box-shadow: 0 0 3px 2px var(--accent-color);
  outline: none;
}

.newsletter__checkboxContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 22px;
  width: 22px;
  background-color: #eee;
  transition: 0.15s ease-in background-color;
  border-radius: 5px;
}

.newsletter__checkboxContainer:hover input ~ .checkmark {
  transition: 0.15s ease-in background-color;
  background-color: #ccc;
}

.newsletter__checkboxContainer input:checked ~ .checkmark {
  transition: 0.15s ease-in background-color;
  background-color: var(--accent-color);
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.newsletter__checkboxContainer input:checked ~ .checkmark:after {
  display: block;
}

.newsletter__checkboxContainer .checkmark:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.col--contact {
  width: 100%;
}

.contact__tel {
  display: block;
  margin-top: 16px;
  margin-bottom: 40px;
  color: var(--white-color);
  font-size: clamp(1rem, 0.875rem + 0.625vw, 1.375rem);
  text-decoration: none;
  font-family: Arial Black, Arial, Helvetica, sans-serif;
  &&:hover {
    text-decoration: underline;
  }
}

.contact__socialWrapper {
  display: flex;
  gap: 12px;
}

.Footer__copyright {
  text-align: center;
}

@media screen and (max-width: 1024px) {
  .Footer {
    padding: 80px 30px 20px;
  }

  .Footer__colWrapper {
    flex-direction: column;
    align-items: center;
    gap: clamp(2.5rem, 1.932rem + 2.841vw, 3.75rem);
    max-width: 400px;
  }

  .col {
    max-width: 100%;
    text-align: center;
  }

  .contact__socialWrapper {
    justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .Footer {
    padding: 60px 20px 15px;
  }
}

@media screen and (max-width: 480px) {
  .Footer {
    padding: 50px 20px 10px;
  }
}
