.icon {
  transition: ease-in 0.15s transform;
}

.icon--small {
  width: 30px;
  height: 30px;
}

.icon--regular {
  width: 50px;
  height: 50px;
}

.icon:hover {
  transform: scale(110%);
}

@media screen and (max-width: 1024px) {
  .icon {
    width: clamp(1.875rem, 1.449rem + 2.131vw, 2.813rem);
    height: clamp(1.875rem, 1.449rem + 2.131vw, 2.813rem);
  }
}