:root {
  --container-max-width: 1280px;
  --color-primary: #001f3f;
  --color-accent: #ff851b;
}

.HomePage__banner {
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
  height: 600px;
  margin: 0 auto;
  max-width: var(--container-max-width);
  position: relative;
  overflow: hidden;
  &&::after {
    z-index: 0;
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 15vw;
    background-image: linear-gradient(
      0deg,
      rgba(0, 31, 63, 1) 0%,
      rgba(0, 31, 63, 0) 100%
    );
  }
}

.banner__content {
  margin-top: 80px;
  max-width: 700px;
  z-index: 1;
  display: flex;
  flex-direction: column;
}

.banner__p {
  max-width: 450px;
}

.banner__h1 {
  font-size: clamp(1.625rem, 0.917rem + 3.542vw, 3.75rem);
  line-height: calc(clamp(1.625rem, 0.917rem + 3.542vw, 3.75rem) * 1.15);
}

.banner__h1,
.aboutUs__h2,
.services__h2,
.comments__h2,
.cta__h2 {
  text-transform: uppercase;
  font-family: Arial Black, Arial, "Helvetica Neue", Helvetica,
  sans-serif;
  margin-bottom: 24px;
}

.banner__imgWrap {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.banner__img {
  position: absolute;
  width: 700px;
  left: 600px;
  top: 0;
}

.banner__img--tablet,
.banner__img--mobile {
  display: none;
}

.HomePage__aboutUs {
  align-items: center;
  display: flex;
  justify-content: center;
  gap: 60px;
  max-width: var(--container-max-width);
}

.aboutUs__content {
  max-width: 500px;
}

.aboutUs__h2,
.services__h2,
.comments__h2,
.cta__h2 {
  font-size: clamp(1.625rem, 1.167rem + 2.292vw, 3rem);
  line-height: calc(clamp(1.625rem, 1.167rem + 2.292vw, 3rem) * 1.15);
}

.banner__p,
.aboutUs__p,
.cta__p {
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  line-height: calc(clamp(1rem, 0.917rem + 0.417vw, 1.25rem) * 1.5);
  margin-bottom: 40px;
  text-wrap: balance;
}

.aboutUs__img {
  max-width: 550px;
  width: 100%;
}

.HomePage__aboutUs,
.HomePage__services,
.HomePage__comments {
  margin: 180px auto 0;
  max-width: var(--container-max-width);
}

.services__h2,
.comments__h2 {
  margin-bottom: 70px;
  text-align: center;
}

.HomePage__comments {
  margin-bottom: 180px;
}

.services__content {
  display: flex;
  justify-content: center;
  gap: 30px 60px;
  flex-wrap: wrap;
}

.HomePage__cta {
  background-image: url("../../img/home_CTA--fireworksPng.png");
  background-size: contain;
  background-position: center;
  background-repeat: repeat-x;
  padding: clamp(3.75rem, 2.083rem + 8.333vw, 8.75rem) 20px;
  background-color: var(--color-accent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-primary);
  text-align: center;
}

.cta__h2,
.cta__p {
  max-width: 600px;
}

.cta__h2 {
  z-index: 1;
}

.cta__p {
  z-index: 1;
}

.cta__waves {
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
  && svg {
    width: calc(100% + 1.3px);
    height: 90px;
  }
}

.shape-fill {
  fill: var(--color-accent);
}

@media screen and (max-width: 1280px) {
  .HomePage__banner {
    margin-inline-start: 40px;
  }
}

@media screen and (max-width: 1024px) {
  .HomePage__banner {
    align-items: center;
    gap: 40px;
    overflow: visible;
    height: unset;
  }
  .banner__imgWrap {
    position: static;
  }
  .banner__img {
    display: none;
  }
  .banner__img--tablet {
    display: block;
    max-width: 600px;
    width: 100%;
  }

  .HomePage__comments,
  .HomePage__aboutUs,
  .HomePage__banner {
    flex-direction: column;
    text-align: center;
    margin-inline: 20px;
  }

  .HomePage__services {
    margin-inline: 20px;
  }

  .banner__p {
    align-self: center;
  }
  .aboutUs__img {
    max-width: 400px;
  }
}

@media screen and (max-width: 768px) {
  .HomePage__cta {
    background-image: none;
  }
}
