.GalleryCard {
  max-width: 600px;
  width: 100%;
  background-color: #153c65;
}

.GalleryCard__img {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.15s ease-in transform;
  width: inherit;
  height: 350px;
  &&:hover {
    transform: scale(105%);
  }
}
