.TeamMemberCard {
  max-width: 330px;
  width: 100%;
  background-color: #153c65;
}

.TeamMemberCard__img {
  max-width: inherit;
  width: 100%;
}

.TeamMemberCard__content {
  padding: 20px 20px 30px;
}
.TeamMemberCard__h3 {
  margin-bottom: 12px;
  text-transform: uppercase;
  font-size: clamp(1.125rem, 1.042rem + 0.417vw, 1.375rem);
}

.TeamMemberCard__h4 {
  margin-bottom: 20px;
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
}
.TeamMemberCard__p {
  line-height: calc(clamp(1rem, 0.958rem + 0.208vw, 1.125rem) * 1.4);
  font-size: clamp(1rem, 0.958rem + 0.208vw, 1.125rem);
}