.Input__label {
  display: flex;
  flex-direction: column;
  font-size: clamp(1rem, 0.875rem + 0.625vw, 1.375rem);
  gap: 10px;
  text-transform: capitalize;
  width: 100%;
}

.Input {
  border: 0;
  font-family: inherit;
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  padding: clamp(0.5rem, 0.354rem + 0.729vw, 0.938rem);
  border-radius: 5px;
  &&::placeholder {
    font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  }
}

.Input--round {
  border-radius: 100px;
  padding-inline: clamp(0.938rem, 0.833rem + 0.521vw, 1.25rem);
}

.Input--small {
  padding-block: clamp(0.5rem, 0.458rem + 0.208vw, 0.625rem);
  font-size: clamp(0.875rem, 0.833rem + 0.208vw, 1rem);
  &&::placeholder {
    font-size: clamp(0.875rem, 0.833rem + 0.208vw, 1rem);
  }
}