.Button {
  width: 100%;
  border-radius: 30px;
  font-weight: bold;
  font-size: 16px;
  text-transform: capitalize;
  cursor: pointer;
  transition: ease-in 0.15s border-color;
  &&:hover {
    transition: ease-in 0.15s border-color;
  }
}

.Button--small {
  max-width: 180px;
  padding: 14px 20px;
  font-size: 14px;
}

.Button--regular {
  max-width: 230px;
  padding: 16px 25px;
  font-size: 16px;
}
.Button--large {
  max-width: 250px;
  padding: 16px 25px;
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
}
.Button--extraLarge {
  max-width: 300px;
  padding: 16px 25px;
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
}
.Button--orange {
  background-color: #ff851b;
  color: #fff;
  border: 1px solid #ff851b;
  &&:hover {
    border-color: #ffffff;
  }
}
.Button--white {
  background-color: #ffffff;
  color: #001f3f;
  border: 1px solid #ffffff;
  &&:hover {
    border-color: #001f3f;
  }
}
.Button--fullWidth {
  max-width: none;
}
