.ContactForm {
  background-color: #153c65;
  max-width: 800px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  gap: clamp(1.875rem, 1.25rem + 3.125vw, 3.75rem);
  padding: clamp(1.25rem, 0.833rem + 2.083vw, 2.5rem)
    clamp(0.938rem, 0.417rem + 2.604vw, 2.5rem);
  container-name: contactForm;
  container-type: inline-size;
}

.contactForm__wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  gap: 20px;
}

.inputWrapper {
  display: flex;
  flex-direction: row;
  gap: 25px;
  width: 100%;
}

.form__label {
  display: flex;
  flex-direction: column;
  font-size: clamp(1rem, 0.875rem + 0.625vw, 1.375rem);
  gap: 10px;
  text-transform: capitalize;
  width: 100%;
}

.form__input {
  border: 0;
  font-family: inherit;
  font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  padding: clamp(0.5rem, 0.354rem + 0.729vw, 0.938rem);
  border-radius: 5px;
  &&::placeholder {
    font-size: clamp(1rem, 0.917rem + 0.417vw, 1.25rem);
  }
}

.form__input--message {
  height: 150px;
}

@container contactForm (max-width: 650px) {
  .inputWrapper {
    flex-direction: column;
  }
}