/* Стили для виджета */
.widgetButton {
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 80px;
  height: 80px;
  background-color: #ff851b;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  transition: transform 0.3s ease;
}

.widgetButton:hover {
  transform: scale(1.1);
}

.phoneIcon {
  width: 40px;
}

.closeIcon {
  width: 20px;
}

/* Стили для формы */
.formContainer {
  /* color: #001F3F; */
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  max-width: 400px;
  padding: 40px 30px;
  background-color: #153c65;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.formHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.formHeader h3 {
  font-size: clamp(1.125rem, 1rem + 0.625vw, 1.5rem);
  text-transform: uppercase;
}

.closeButton {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.formLabel {
  font-size: 14px;
  margin-bottom: 10px;
}

.formInput {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 15px;
  width: 100%;
  font-size: 14px;
}

.submitButton {
  background-color: #ff851b;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.submitButton:hover {
  background-color: #e04e4e;
}

@media screen and (max-width: 480px) {
  .widgetButton {
    bottom: 20px;
    right: 20px;
    width: 70px;
    height: 70px;
  }

  .phoneIcon {
    width: 30px;
  }
}
