.CommentCard {
  max-width: 300px;
  width: 100%;
  background-color: #153c65;
  padding: 20px 25px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.CommentCard__content {
  display: flex;
  gap: 12px;
  align-items: center;
}

.CommentCard__avatar {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: #D9D9D9;
  display: flex;
  justify-content: center;
  align-items: center;
  && img {
    width: 30px;
    height: 30px;
  }
}

.CommentCard__text {
  && h3 {
    margin-bottom: 6px;
  }
}

.CommentCard__comment {
  line-height: 1.4;
  /* max-height: calc(1.15*6rem);
  overflow: hidden; */
}